      const material = new THREE.MeshPhongMaterial();
      this.obj[counter].children[0].material = material
      this.obj[counter].children[0].material.color.set(Number(code))
      if(this.bump) {
        const texture = new THREE.TextureLoader().load( this.bump );
        this.obj[counter].children[0].material.bumpMap = texture
      }
      this.obj[counter].children[0].material.shininess = shininess
    },
    setcolor2(counter , code , shininess) {
      this.obj[counter].children[0].material.bumpMap = null
      this.obj[counter].children[0].material.color = null
      const texture2 = new THREE.TextureLoader().load(code);
      this.obj[counter].children[0].material.map = texture2
      const texture = new THREE.TextureLoader().load( 'textures/land_ocean_ice_cloud_2048.jpg' );
      const material = new THREE.MeshPhongMaterial( { map: texture2 } );
      this.obj[counter].children[0].material = material
      this.obj[counter].children[0].material.shininess = shininess
    },
    setbump( link ,idx){
      this.bumped = idx
      this.bump = link
      document.querySelectorAll('.tabha').forEach(item => {
        item.style.background = '#cccccc'
      })
      document.getElementById('tab' + this.bumped+ '-' + this.counter).style.background = '#ececec'
    },
    getLayoutNavbarBg () {
      return this.layoutNavbarBg
    },
  }
}
</script>
<style>
body{
  overflow:hidden
}
</style>